import React, { useState } from "react";
import { Button, Col, FormGroup, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../../reducers/homeReducer";
import { ErrorAlert, SuccessAlert } from "../../shared-components/AlertBox";
import AddSkillsComponentModal from "../../shared-components/AddSkillsComponentModal";
import Skills from "../../shared-components/Skills";

const ProfileSkills = (props) => {
  const homeData = props.homeData;
  const [skills, setSkills] = useState(homeData.skills);
  const { userdetails } = useSelector((state) => state.user);
  const [addSkillsModal, setAddSkillsModal] = useState(false);
  const [updateSkill, setUpdateSkill] = useState(true);
  const { error, success } = {};
  const formFieldTypes = [
    "skillName-text-require",
    "skillHeading-text-optional",
    "skillImage-image-optional",
    "skillDesc-quill-optional",
  ];
  const [formFieldValues, setFormFieldValues] = useState({});
  const dispatch = useDispatch();

  const updateStatus = async () => {
    await dispatch(
      updateHomeReducer({
        ...homeData,
        skills: { ...skills, active: !skills.active },
        token: userdetails.token,
      })
    );
    props.updateData({
      ...homeData,
      skills: { ...skills, active: !skills.active },
    });
    setSkills({ ...skills, active: !skills.active });
  };

  const closeModal = () => {
    setAddSkillsModal(false);
    setUpdateSkill(true);
    setFormFieldValues({});
  };
  const openModal = () => {
    setAddSkillsModal(true);
    setUpdateSkill(false);
  };

  const editSkills = async (sName, sHeading, sImage, sDesc, id) => {
    await setFormFieldValues({
      ...formFieldValues,
      skillName: sName,
      skillHeading: sHeading,
      skillImage: sImage,
      skillDesc: sDesc,
      id,
    });
    setAddSkillsModal(true);
    setUpdateSkill(false);
  };

  return (
    <>
      <Row>
        <Col className="col-6 col-md-3 order-2 order-md-1 px-4">Active : </Col>
        <Col className="col-6 col-md-3 order-2 order-md-1">
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={skills.active}
              onChange={updateStatus}
            />
            {skills.active ? "Active" : "Inactive"}
          </FormGroup>
        </Col>
        <Col className="text-end col-12 col-md-6 order-1 order-md-2">
          <Button
            className="sp-btn-secondary rounded-0 px-5 mb-4 mb-md-0"
            onClick={openModal}
          >
            Add New Skill
          </Button>
        </Col>
      </Row>

      {addSkillsModal && (
        <AddSkillsComponentModal
          setAddSkillsModal={closeModal}
          formField={formFieldTypes}
          formInputValues={formFieldValues}
        />
      )}
      <Skills
        displayType="list"
        updateSkill={updateSkill}
        editSkills={editSkills}
      />
      {error && <ErrorAlert message={error} />}
      {success && <SuccessAlert message={success} />}
    </>
  );
};

export default ProfileSkills;
