import React, { useState } from "react";
import { useInView } from "react-intersection-observer";
import "react-quill/dist/quill.snow.css";
import { Col, FormGroup, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../../reducers/homeReducer";
import axios from "axios";
import { API_URL } from "../../utils/API_URl";
import FormElementComponent from "../../shared-components/FormElementComponent";

const EditHomeContent1 = (props) => {
  const [pageContent1, setPageContent1] = useState(props.homeData.pageContent1);
  let homeData = props.homeData;
  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const { ref, inView } = useInView({
    triggerOnce: true, // Only trigger the animation once
    threshold: 0.1, // Percentage of section visibility to trigger the animation
  });

  const formField = [
    "contentTitle-quill-require",
    "contentHeading-quill-optional",
    "contentData-quill-optional",
    "attachment-file-optional",
  ];
  const [formInputValues, setFormInputValues] = useState(pageContent1);

  const closeModal = () => {
    props.cancelEditContent1(false);
  };

  const createData = () => {};

  const updateData = async (inputData, file) => {
    let attachments = {};

    if (file !== null) {
      const promiseArray = await formField.map(async (item, i) => {
        let itemData = item.split("-");
        let itemField = itemData[0];
        if (itemData[1] === "image" || itemData[1] === "file") {
          if (file[itemField] === null) {
            return null;
          } else if (file[itemField] === "No File") {
            let preFieldData = formInputValues[itemField];
            let preFileExt = preFieldData.split(".").splice(-1)[0];
            if (
              ["jpg", "jpeg", "png", "gif"].includes(preFileExt) === true &&
              itemData[1] === "image"
            ) {
              await axios.delete(
                `${API_URL}/api/deleteFiles/uploads/${preFieldData}`
              );
            } else {
              await axios.delete(
                `${API_URL}/api/deleteFiles/uploadedFiles/${preFieldData}`
              );
            }
            attachments = { ...attachments, [itemField]: null };
          } else {
            const formData = new FormData();
            await formData.append("image", file[itemField]);

            if (inputData[itemField] !== null) {
              let inputFileExt = inputData[itemField].split(".").splice(-1)[0];
              if (
                ["jpg", "jpeg", "png", "gif"].includes(inputFileExt) === true &&
                itemData[1] === "image"
              ) {
                await axios.delete(
                  `${API_URL}/api/deleteFiles/uploads/${inputData[itemField]}`
                );
              } else {
                await axios.delete(
                  `${API_URL}/api/deleteFiles/uploadedFiles/${inputData[itemField]}`
                );
              }
            }

            let formFileExt = file[itemField].name.split(".").splice(-1)[0];
            if (
              ["jpg", "jpeg", "png", "gif"].includes(formFileExt) === true &&
              itemData[1] === "image"
            ) {
              const res = await axios.post(`${API_URL}/api/upload/`, formData, {
                headers: {
                  "Content-Type": "multipart/form-data",
                  token: userdetails.token,
                },
              });
              attachments = { ...attachments, [itemField]: res.data.filename };
            } else {
              const res = await axios.post(
                `${API_URL}/api/uploadDoc/`,
                formData,
                {
                  headers: {
                    "Content-Type": "multipart/form-data",
                    token: userdetails.token,
                  },
                }
              );
              attachments = { ...attachments, [itemField]: res.data.filename };
            }
          }
        }
      });
      Promise.all(promiseArray).then(async () => {
        await setPageContent1({
          ...inputData,
          ...attachments,
        });
        await dispatch(
          updateHomeReducer({
            ...homeData,
            pageContent1: { ...inputData, ...attachments },
            token: userdetails.token,
          })
        );
        await props.cancelEditContent1(false);
      });
    } else {
      await setPageContent1({ ...inputData });
      await dispatch(
        updateHomeReducer({
          ...homeData,
          pageContent1: { ...inputData },
          token: userdetails.token,
        })
      );
      await props.cancelEditContent1(false);
    }
  };

  return (
    <div ref={ref} className={inView ? "animated-section" : "hidden-section"}>
      <Row>
        <Col md={6}>Active : </Col>
        <Col md={6}>
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={pageContent1.active}
              disabled
            />
            {pageContent1.active ? "Active" : "Inactive"}
          </FormGroup>
        </Col>
      </Row>
      <FormElementComponent
        formField={formField}
        formInputValues={formInputValues}
        modalHeading={"Update Home Top Banner"}
        buttons={[
          {
            type: "cancel",
            title: "Cancel",
          },
          {
            type: "update",
            title: "Update",
          },
        ]}
        closeModal={closeModal}
        createData={createData}
        updateData={updateData}
        formType="modal"
      />
    </div>
  );
};

export default EditHomeContent1;
